body{
    display: flex;
    background-color: rgb(32, 32, 39);
    justify-content: center;
    align-items: center;
}
.main_container {
    padding: 20px;
    margin: 20px;
    background-color: white;
    width: 800px;
    height: 600px;
    border-radius: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.button_dice{
    border: none;
    box-shadow: 0px 2px 3px 0px rgba(0,0,0,0.4);
    width: 60px;
    height: 60px;
    margin: 20px;
    font-size: 30px;
    font-weight: 700;
    border-radius: 10px;
    cursor: pointer;
}
.main2_container{
    display: grid;
    grid-template: auto auto / repeat(5, 1fr);
}
.btn1{
    margin-top: 40px;
    display: flex;
    justify-content: center;
}
.roll{
    border: none;
    padding: 16px;
    font-size: 28px;
    font-weight: 500;
    width: 180px;
    color: white;
    background-color: blue;
    border-radius: 8px;
    box-shadow: 0px 2px 8px 2px rgba(0,0,0,0.4);
    cursor: pointer;

}
.roll:hover{
    background-color: rgb(2, 2, 148);
}
.roll:active{
    box-shadow: 0px 2px 8px 6px rgba(0,0,0,0.4);
}
.main {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    inline-size: 50%;
    
    
}
h1 {
    font-size: 44px;
    margin-bottom: -2px;
}
p {
    font-size: 22px;
}
.result_container {
    position: absolute;
    background-image: linear-gradient(rgb(0, 17, 255), rgb(4, 196, 255));
    border-radius: 80px;
    color: white;
    top: 15%;
    padding: 20px;
    font-size: 20px;
}
.highscores {
    position: relative;
    right: -300px;
    border: none;
    padding: 12px;
    font-size: 28px;
    font-weight: 500;
    width: 180px;
    color: white;
    background-color: blue;
    border-radius: 8px;
    box-shadow: 0px 2px 8px 2px rgba(0,0,0,0.4);
    cursor: pointer;
}
.highscore_container {
    position: absolute;
    background-image: linear-gradient(rgb(0, 17, 255), rgb(4, 196, 255));
    border-radius: 80px;
    color: white;
    top: 10%;
    padding: 30px;
    font-size: 20px;
    width: 400px;
    height: 400px;
}
.highscore_container {
    overflow: scroll;
  }
  .highscore_container::-webkit-scrollbar {
    width: 8px;
  }
  .highscore_container {
    scrollbar-width: thin;
  }
  
.close_highscores{
    position: relative;
    bottom: -40px;
    border: none;
    padding: 12px;
    font-size: 28px;
    font-weight: 500;
    width: 180px;
    color: white;
    background-color: blue;
    border-radius: 8px;
    box-shadow: 0px 2px 8px 2px rgba(0,0,0,0.4);
    cursor: pointer;
}
.score_container{
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 14px;
    max-height: 300px;
    overflow-y: auto;
}
.score_container2{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.scores_title {
    margin-top: -10px;
}
.highscore-row {
    margin-bottom: 10px;
}
.save_button, .close_button {
    margin-top: 16px;
    border: none;
    font-size: 28px;
    font-weight: 500;
    width: 100px;
    color: white;
    background-color: blue;
    border-radius: 8px;
    box-shadow: 0px 2px 8px 2px rgba(0,0,0,0.4);
    cursor: pointer;
    margin: 10px;
    margin-top: 18px;
}
.input_save{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.name_input{
    font-size: 18px;
    border-radius: 8px;
    background-color: rgb(175, 207, 255);
}
hr{
    border: 1px solid rgba(255, 255, 255, 0.452);
}
